.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}
#searchBar {
  position: fixed;
  margin-left: auto;
  margin-right: auto;

  padding-top: 15px;
  padding-left:40%;
  width: 60%;
  z-index: 11;

}
#mapContainer {
  height: 100%;
}
#LeftDiv{
  height: auto;
  width: auto;
  position: fixed;
  z-index: 10;
  top: 50px;
  padding-top: 15px;
  left: 15px;
  background-color: white;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



